import { TableBody, TableCell,Table,TableHead,TableRow,styled, Button } from "@mui/material";

import { deleteCand, getCand } from "../service/api";

import { useEffect , useState} from "react";
import { Link } from "react-router-dom";


import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';






const Thead = styled (TableRow)`
     background: #06bf97;
     & > th {
        color: #fff;
        font-size:20px;
     }
`

const Tbody = styled (TableRow)`
     & > td {
        
        font-size:20px;
     }
`
const StyledTable = styled (Table)`
     width: 90%;
     margin: 50px auto 0 auto;
`


const AllCandidates = () =>{
    const [cand, setCand] = useState ([]);
    useEffect(()=>{
       getCandidateDetails();
    },[])

    const getCandidateDetails =async () =>{
        let response  =await getCand();
        console.log(response);
        setCand(response.data);
    }

    const deleteCandData = async(id) =>{
        await deleteCand(id);
        getCandidateDetails();
    }

    return(
        <StyledTable >
           <TableHead>
               <Thead>
                  <TableCell>Id</TableCell>
                  <TableCell>Recruitment Name</TableCell>
                  <TableCell>Role</TableCell>
                  <TableCell>Level</TableCell>
                  <TableCell>Other</TableCell>
                  <TableCell>Description</TableCell>
                  <TableCell>Actions</TableCell>
             </Thead>
          </TableHead>

           <TableBody>
            {
                cand.map(user=>(
                    <Tbody>
                        <TableCell>{user.id}</TableCell>
                        <TableCell>{user.name}</TableCell>
                        <TableCell>{user.role}</TableCell>
                        <TableCell>{user.level}</TableCell>
                        <TableCell>{user.other}</TableCell>
                        <TableCell>{user.desc}</TableCell>
                        <TableCell>
                            <Button  style={{marginRight:10}} component={Link} to ={`/edit/${user.id}`}> <EditIcon/></Button>
                            <Button  color="secondary" onClick={()=>deleteCandData(user.id)}><DeleteIcon/> </Button>
                        </TableCell>
                    </Tbody>
                ))
            }
           </TableBody>
           </StyledTable>

           

    )
}

export default AllCandidates;

