import axios from 'axios';

const API_URL ='http://localhost:3002/users';

export const addCand = async (data) =>{
    try {
     return await axios.post(API_URL, data)
} catch(error){
    console.log("Error While Calling Add User Api", error.message);
}
}



export const getCand = async()=>{
    try{
        return await axios.get(API_URL);

    } catch (error){
       console.log("error while calling getCandidaites API" , error.message);
    }
}

export const getOneCand = async(data)=>{
    try{
        return await axios.get(`${API_URL}/${data}`);

    } catch (error){
       console.log("error while calling getOneCandidaites API" , error.message);
    }
}

export const eidtCand = async(data, id)=>{
    try{
        return await axios.put(`${API_URL}/${id}`,data);
    } catch (error){
       console.log("error while calling Edit User API" , error.message);
    }
}

export const deleteCand = async (id) =>{
    try{
        return await axios.delete(`${API_URL}/${id}`);

    }catch(error){
        console.log("Error While Calling Delete Api" , error.message)
    }
}
