import { FormControl, FormGroup, styled, Input, InputLabel,NativeSelect,TextareaAutosize, Typography,Button } from "@mui/material";
import {useState} from 'react';
import { addCand } from "../service/api";
import { useNavigate } from "react-router-dom";


import SaveAltSharpIcon from '@mui/icons-material/SaveAltSharp';
import HomeSharpIcon from '@mui/icons-material/HomeSharp';

const Container = styled (FormGroup)`
width:50%;
margin:5% auto 0 auto;
& >div  {
    margin-top: 15px
}
`
const Bt = styled (Button)`
     width:100px;
     height:40px;
`

const initialValues = {
    name: '',
    role: '',
    level: '',
    other: '',
    desc: ''

}


function AddCandidates() {
    const [cand, setCand] = useState(initialValues);
    const navigate = useNavigate();

    const onValueChange =(e)=>{
        setCand({...cand , [e.target.name]:e.target.value})
        console.log(cand);
        }

    const addCandidateDetails = async () =>{
        await addCand(cand);
        navigate('/all');
    }
    const backToHome = () =>{
        navigate('/home');
    }
    return (
        <Container>

                <Typography variant="h4"> Add Recruitment</Typography>
            <FormControl>
            <InputLabel>Enter The name of your recruitment</InputLabel>
            <Input onChange={(e)=> onValueChange(e)} name ="name"/>
            </FormControl>
            
            <FormControl>
            <InputLabel variant="standard" htmlFor="uncontrolled-native">
             Role:
            </InputLabel>
            <NativeSelect onChange={(e)=> onValueChange(e)} name ="role"
            defaultValue={"other"}
            inputProps={{
            name: 'role',
            id: 'uncontrolled-native',
            }}
       >
           <option value={"Other"}>Other</option>
           <option value={"Intern"}>Intern</option>
           <option value={"Employee"}>Employee</option>
            </NativeSelect>
        
            </FormControl>

            <FormControl>
            <InputLabel variant="standard" htmlFor="uncontrolled-native">
             Select level of employee:
            </InputLabel>
            <NativeSelect onChange={(e)=> onValueChange(e)} name ="level"
            defaultValue={""}
            inputProps={{
            name: 'level',
            id: 'uncontrolled-native',
            }}
       >
           <option value={"beginer"}>Beginer</option>
           <option value={"intermediate"}>Intermediate</option>
           <option value={"pro"}>Pro</option>
            </NativeSelect>
            </FormControl>

            <FormControl>
            <InputLabel> Other</InputLabel>
            <Input onChange={(e)=> onValueChange(e)} name ="other"/>
            </FormControl>

            <FormControl>
            <TextareaAutosize onChange={(e)=> onValueChange(e)} name ="desc" maxRows={4}aria-label="maximum height"placeholder="Maximum 4 rows"
            defaultValue=" " 
            style={{ width: 700, height:100 }}
/>
            </FormControl>
            <FormControl>
            <Bt onClick={()=> addCandidateDetails() }  variant= "contained"><SaveAltSharpIcon/></Bt>
            </FormControl>
            <FormControl>
            <Bt onClick={()=> backToHome() } variant="outlined"><HomeSharpIcon/></Bt>
            </FormControl>


            </Container>
    );
}

export default AddCandidates;