import { FormControl, FormGroup, styled, Input, InputLabel,NativeSelect,TextareaAutosize, Typography,Button } from "@mui/material";
import userEvent from "@testing-library/user-event";
import {useState , useEffect} from 'react';
import { useNavigate, useParams } from "react-router-dom";
import { eidtCand, getOneCand } from "../service/api";


import EditIcon from '@mui/icons-material/Edit';

const Bt = styled (Button)`
     width:100px;
     height:40px;
`



const Container = styled (FormGroup)`
width:50%;
margin:5% auto 0 auto;
& >div  {
    margin-top: 15px
}
`
const initialValues = {
    name: '',
    role: '',
    level: '',
    other: '',
    desc: ''

}


function EditCandidates() {
    const [cand, setCand] = useState(initialValues);
    const navigate = useNavigate();
    const {id} = useParams();

    useEffect(()=>{
       getOneCandData();
    },[])

    const getOneCandData = async()=>{
       let response = await getOneCand(id);
       setCand(response.data);
    }

    const onValueChange =(e)=>{
        setCand({...cand , [e.target.name]:e.target.value})
        console.log(cand);
        }

    const eidtCandidateDetails = async () =>{
        await eidtCand(cand,id);
        navigate ('/all');
    }
    const backToHome = () =>{
        navigate('/home');
    }
    return (
        <Container>

                <Typography variant="h4"> Edit User</Typography>
            <FormControl>
            <InputLabel> Enter The name of your recruitment</InputLabel>
            <Input onChange={(e)=> onValueChange(e)} name ="name" value={cand.name} />
            </FormControl>
            
            <FormControl>
            <InputLabel variant="standard" htmlFor="uncontrolled-native">
             Role:
            </InputLabel>
            <NativeSelect onChange={(e)=> onValueChange(e)} name ="role" value={cand.role}
            defaultValue={"other"}
            inputProps={{
            name: 'role',
            id: 'uncontrolled-native',
            }}
       >
           <option value={"Other"}>Other</option>
           <option value={"Intern"}>Intern</option>
           <option value={"Employee"}>Employee</option>
            </NativeSelect>
        
            </FormControl>

            <FormControl>
            <InputLabel variant="standard" htmlFor="uncontrolled-native">
             Select level of employee:
            </InputLabel>
            <NativeSelect onChange={(e)=> onValueChange(e)} name ="level" value={cand.level}
            defaultValue={""}
            inputProps={{
            name: 'level',
            id: 'uncontrolled-native',
            }}
       >
           <option value={"beginer"}>Beginer</option>
           <option value={"intermediate"}>Intermediate</option>
           <option value={"pro"}>Pro</option>
            </NativeSelect>
            </FormControl>

            <FormControl>
            <InputLabel> Other</InputLabel>
            <Input onChange={(e)=> onValueChange(e)} name ="other" value={cand.other}/> 
            </FormControl>

            <FormControl>
            <TextareaAutosize onChange={(e)=> onValueChange(e)} name ="desc" value={cand.desc} maxRows={4}aria-label="maximum height"placeholder="Maximum 4 rows"
            defaultValue=" " 
            style={{ width: 700, height:100 }}
/>
            </FormControl>
            <FormControl>
            <Bt onClick={()=> eidtCandidateDetails() }  variant= "contained"><EditIcon/></Bt>
            </FormControl>
            <FormControl>
            <Bt onClick={()=> backToHome() } variant="outlined">Cancel</Bt>

            </FormControl>
            


            </Container>
    );
}

export default EditCandidates;